import React from 'react';
import axios from 'axios';
import { Formik, Form, Field } from 'formik';


class Adduser extends React.Component {
    state={
        message: '', 
        role: ''
    }

    handleChange = (e) => {
        this.setState({
            role: e.target.value
        })
    }
    render () {
        return (
            <>
                <div className="card-body">

                    <Formik
                        initialValues={{
                            name: '',
                            email: '', 
                            role: '',
                            expire: '',
                            password: ''
                        }}

                        onSubmit={(value, {resetForm}) => {
                            const formdata = new FormData;
                            formdata.append('name', value.name)
                            formdata.append('email', value.email)
                            formdata.append('role', this.state.role)
                            formdata.append('expire', value.expire)
                            formdata.append('password', value.password)

                            axios.post('users', formdata).then(response => {
                                if(response.status === 201){
                                    this.setState({
                                        message: 'Creer avec succes',
                                    })
                                    resetForm();
                                }else {
                                    this.setState({
                                        message: 'Une erreur est survenue lors la validation'
                                    })
                                }
                            })
                        }}
                    >
                        <Form id="notificationDemo">
                            <div className="form-group">
                                <label className="form-label" htmlFor="demoText" _msttexthash="1074619" _msthash="78">Nom etprnom</label>
                                <Field type="text" className="form-control" name="name" id="demoText" placeholder="Nom et prnom" _msthash="79" required />
                            </div>
                            <div className="form-group">
                                <label className="form-label" htmlFor="demoText" _msttexthash="1074619" _msthash="78">E-mail</label>
                                <Field type="email" className="form-control" name="email" id="demoText" placeholder="E-mail" _msthash="79" required/>
                            </div>
                            <div className="form-group">    
                                <label className="form-label" htmlFor="demoText" _msttexthash="1074619" _msthash="78">Rle</label>
                                <select name="role" onChange={this.handleChange} className="form-control" id="exampleFormControlSelect1">
                                    <option>Administrateur</option>
                                    <option>Utilisateur</option>
                                </select>
                            </div>

                            <div className="form-group">
                                <label className="form-label" htmlFor="demoText" _msttexthash="1074619" _msthash="78">Date d'expiration</label>
                                <Field type="date" className="form-control" name="expire" id="demoText" placeholder="E-mail" _msthash="79"/>
                            </div>

                            <div className="form-group">
                                <label className="form-label" htmlFor="demoText" _msttexthash="1074619" _msthash="78">Mot de passe</label>
                                <Field type="password" className="form-control" name="password"  placeholder="Mot de passe" required />
                            </div>
                            
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary" _msttexthash="2631473" _msthash="97">Valider l'utilisateur</button>
                            </div>
                            <p>{this.state.message} </p>
                        </Form>
                    </Formik>
                </div>
            </>
        )
    }
}

export default Adduser;