import React from 'react';
import { Formik } from 'formik';
import ProgressBar from "@ramonak/react-progress-bar";

class Addcandidat extends React.Component {
    state={
        app: '',
        progress: '',
        informatk: [],
        langue: '',
        progrelangue: '',
        langage: [],
        loisArr: [],
        loisir: ''
    }

    
    //onclik Loisir buttn
    handleClickLoisir = () => {
        const loisirState = this.state.loisir
        this.setState(prevState => ({
            loisArr: [{loisirState}, ...prevState.loisArr]
        }))
    }
    
    //onclik informatique
    handleClickInfo = () => {
        const appState = this.state.app
        const progresState = this.state.progress
        this.setState(prevState => ({
            informatk: [{appState, progresState}, ...prevState.informatk]
        }))
    }

    // oncklik langue
    handleClicklangue = () => {
        const langueState = this.state.langue;
        const progresLangueState = this.state.progrelangue;
        this.setState(prevState => ({
            langage: [{langueState, progresLangueState}, ...prevState.langage]
        }))
    }

    // ==============================================================================================================


    // form Connaissance info
    onChangeLoisir = (e) => {

        this.setState({
            loisir: e.target.value
        })
    }

    // form Connaissance info
    onChangeApp = (e) => {

        this.setState({
            app: e.target.value
        })
    }

    // form progess informatique
    onChangeProgress = (e) => {

        this.setState({
            progress: e.target.value
        })
    }

    // Form langgaes
    onChangelangue = (e) => {

        this.setState({
            langue: e.target.value
        })
    }

    onChangeProgrelangue = (e) => {

        this.setState({
            progrelangue: e.target.value
        })
    }


    render() {
        const conLangue = this.state.langage;
        const consInfork = this.state.informatk;
        const consLoisir = this.state.loisArr;
        return (
            <>
            <div className="adminx-content">
                <div className="adminx-main-content">  
                <div className="container-fluid">
                       
                    <nav aria-label="breadcrumb" role="navigation">
                        <ol className="breadcrumb adminx-page-breadcrumb">
                           <li className="breadcrumb-item"><a href="#">Tableau de bord</a></li>
                           <li className="breadcrumb-item"><a href="#">Formulaire</a></li>
                           <li className="breadcrumb-item active  aria-current=" page="">Ajouter un CV de candidat</li>
                       </ol>
                       </nav>

                       <div className="pb-3">
                            <h1>Ajouter un CV de candidat</h1>
                       </div>

                       <div className="row">
                        <div className="col-lg-6">
                            <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">A propos du candidat</div>
                                </div>
                                <div className="card-body">
                                    <div className="form-group">
                                    <label className="form-label">Nom et prnom</label>
                                        <input className="form-control mb-2 input-credit-card" type="text" placeholder="Enter credit card number"/>
                                    </div>

                                    <div className="form-group">
                                    <label className="form-label">Post</label>
                                            <input className="form-control input-date mb-2" type="text" placeholder="YYYY/MM/DD"/>
                                    </div>

                                    <div className="form-group">
                                    <label className="form-label">E-mial</label>
                                    <input className="form-control input-numeral mb-2" type="text" placeholder="Enter a large number"/>
                                    </div>

                                    <div className="form-group">
                                    <label className="form-label">Tlphone</label>
                                    <input className="form-control input-prefix mb-2" type="number"/>
                                    </div>

                                    <div className="form-group">
                                    <label className="form-label">Age</label>
                                    <input className="form-control input-prefix mb-2" type="number"/>
                                    </div>

                                    <div className="form-group">
                                    <label className="form-label">Adresse exacte</label>
                                    <input className="form-control input-prefix mb-2" type="text"/>
                                    </div>

                                </div>
                                
                            </div>
                                <div className="card mb-grid">
                                    <div className="card-header">
                                        <div className="card-header-title">Experience</div>
                                    </div>
                                        <div className="card-body">
                                        <div >
                                            <div className="form-group">
                                                <label className="form-label">Nom de l'entreprise</label>
                                                    <input className="form-control mb-2 input-credit-card" 
                                                    type="textarea" placeholder="Socit" />
                                            </div>
                                        </div>

                                        <div>
                                            <div className="form-group">
                                                <label className="form-label">Date</label>
                                                    <input className="form-control mb-2 input-credit-card" 
                                                    type="date" placeholder="JJ/MM/AAAA" />
                                            </div>
                                        </div>

                                        <label className="form-label">Description</label>
                                            <textarea className="form-control mb-2 input-credit-card" 
                                            type="textarea" placeholder="Description de votre profile" rows="10" maxLength={650} >
                                            </textarea>
                                        </div>
                                </div>
                                <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">Connaissance en informatique</div>
                                </div>
                                <div className="card-body">

                                    <table class="table table-sm mb-0">
                                        <thead>
                                        <tr>
                                            <th scope="col">Logiel</th>
                                            <th scope="col">Progression</th>
                                            <th scope="col">Action</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                            {consInfork && consInfork.map(info => {
                                                const app = info.appState;
                                                const progress = info.progresState;
                                                return (
                                                    <>
                                                        <tr>
                                                            <td>{app}</td>
                                                            <td>
                                                                <ProgressBar completed={progress} />
                                                            </td>
                                                            <td>
                                                                <button class="btn btn-sm btn-danger">Effacer</button>
                                                            </td>
                                                        </tr>
                                                    </>
                                                )
                                            })}
                                        </tbody>
                                    </table>

                                    <div >
                                        <div className="form-group">
                                            <label className="form-label">Connaissance</label>
                                                <input className="form-control mb-2 input-credit-card" 
                                                type="text" onChange={this.onChangeApp} placeholder="Logiciel ou autres application" />
                                        </div>
                                    </div>

                                    <div>
                                        <div className="form-group">
                                            <label className="form-label">Progression</label>
                                                <input className="form-control mb-2 input-credit-card" 
                                                type="number" onChange={this.onChangeProgress} placeholder="0-100" min={0} max={100} />
                                        </div>
                                    </div>

                                    <button class="btn btn-primary mr-2" onClick={this.handleClickInfo} >Ajouter</button>
                            </div>   
                            </div>

                            <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">Photo</div>
                                </div>
                                <div className="card-body">
                                    <div >
                                        <div className="form-group">
                                            <label className="form-label">Ajouter une photo</label>
                                                <input className="form-control mb-2 input-credit-card" 
                                                type="file" placeholder="Ecole" />
                                        </div>
                                    </div>
                            </div>                               
                            
                        </div>
                           

                        </div>
                        {/* colonne */}
                        <div className="col-lg-6">
                           <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">Reseaux sociaux</div>
                                </div>
                                <div className="card-body">
                                        <div className="form-group">
                                            <label className="form-label">Lien Facebook</label>
                                                <input className="form-control mb-2 input-credit-card" type="text" placeholder="Lien facebook"/>
                                        </div>
                                        <div className="form-group">
                                            <label className="form-label">Linkedin</label>
                                                <input className="form-control mb-2 input-credit-card" type="text" placeholder="Lien Linkedin"/>
                                        </div>
                                    
                                </div>
                                <div className="card-footer">
                                    <a href="#" target="_blank">Flatpickr</a> is a light-weight library for picking dates and times. It is feature rich and supports date ranges, disabling dates, multiple dates and many more.
                                </div>
                           </div>

                            {/* colonne */}
                           <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">Description du candidat</div>
                                </div>
                                <div className="card-body">
                                    <div className="form-group">
                                    <label className="form-label">Description du profile</label>
                                        <textarea className="form-control mb-2 input-credit-card" 
                                        type="textarea" placeholder="Description de votre profile" rows="10" maxLength={650} >
                                        </textarea>
                                    </div>
                                    </div>
                                <div className="card-footer">
                                    <a href="#" target="_blank">Flatpickr</a> is a light-weight library for picking dates and times. It is feature rich and supports date ranges, disabling dates, multiple dates and many more.
                                </div>
                           </div>

                            
                            <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">Diplme</div>
                                </div>
                                <div className="card-body">
                                    <div >
                                        <div className="form-group">
                                            <label className="form-label">Nom de l'institution</label>
                                                <input className="form-control mb-2 input-credit-card" 
                                                type="textarea" placeholder="Ecole" />
                                        </div>
                                    </div>

                                    <div>
                                        <div className="form-group">
                                            <label className="form-label">Date</label>
                                                <input className="form-control mb-2 input-credit-card" 
                                                type="date" placeholder="JJ/MM/AAAA" />
                                        </div>
                                    </div>

                                    <label className="form-label">Description</label>
                                        <input className="form-control mb-2 input-credit-card" 
                                        type="textarea" placeholder="Description de votre profile" />
                                    </div>
                            </div>        

                            <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">Langues</div>
                                </div>
                                <div className="card-body">
                                    
                                    <table class="table table-sm mb-0">
                                        <thead>
                                        <tr>
                                            <th scope="col">Logiel</th>
                                            <th scope="col">Progression</th>
                                            <th scope="col">Action</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                            {conLangue && conLangue.map(lan => {
                                                const lang = lan.langueState;
                                                const progreLang = lan.progresLangueState;
                                                return (
                                                    <>
                                                        <tr>
                                                            <td>{lang}</td>
                                                            <td>
                                                                <ProgressBar completed={progreLang} />
                                                            </td>
                                                            <td>
                                                                <button class="btn btn-sm btn-danger">Effacer</button>
                                                            </td>
                                                        </tr>
                                                    </>
                                                )
                                            })}
                                        </tbody>
                                    </table>

                                    <div >
                                        <div className="form-group">
                                            <label className="form-label">Ajouter une langue</label>
                                                <input className="form-control mb-2 input-credit-card" 
                                                type="text" onChange={this.onChangelangue} placeholder="Ajouter une langue" />
                                        </div>
                                    </div>

                                    <div>
                                        <div className="form-group">
                                            <label className="form-label">Progression</label>
                                                <input className="form-control mb-2 input-credit-card" 
                                                type="number" onChange={this.onChangeProgrelangue} placeholder="0-100" min={0} max={100} />
                                        </div>
                                    </div>

                                    <button class="btn btn-primary mr-2" onClick={this.handleClicklangue} >Ajouter</button>
                            </div>                               
                            
                        </div>
                        <div className="card mb-grid">
                                <div className="card-header">
                                    <div className="card-header-title">Loisirs</div>
                                </div>
                                <div className="card-body">
                                    <table class="table table-sm mb-0">
                                        <thead>
                                        <tr>
                                            <th scope="col">Loisir</th>
                                            
                                            <th scope="col">Action</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                            {consLoisir && consLoisir.map(loi => {
                                                const loisir = loi.loisirState;
                                                return (
                                                    <>
                                                        <tr>
                                                            <td>{loisir}</td>
                                                            <td>
                                                                <button class="btn btn-sm btn-danger">Effacer</button>
                                                            </td>
                                                        </tr>
                                                    </>
                                                )
                                            })}
                                        </tbody>
                                    </table>
                                    <div >
                                        <div className="form-group">
                                            <label className="form-label">Ajouter une Loisir</label>
                                                <input onChange={this.onChangeLoisir} className="form-control mb-2 input-credit-card" 
                                                type="text" placeholder="Ajouter une loisir" />
                                        </div>
                                    </div>
                                    <button class="btn btn-primary mr-2" onClick={this.handleClickLoisir} >Ajouter</button>
                            </div>                               
                            
                        </div>
                        </div>
                        </div>

                        

                      
                   </div>
                    </div>
               </div>
               
            </>
        )
    }
}

export default Addcandidat;